
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'poppins',sans-serif;
}
.header {
    min-height: 100vh;
    width: 100%;
    background-image: linear-gradient(rgba(208, 126, 126, 0.3),rgba(0,0,0, 0.3));
    background-size: cover;
    background-position: center;
}
nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 7%;
}
.logo{
     width: 50px;
     cursor: pointer;
}
.nav-links li{
    list-style: none;
    display: inline-block;
    margin: 10px 30px;
}
.nav-links li a{
    text-decoration: none;
    color: #ffffff;
}
.nav-links li a:hover{
    color: rgb(0, 0, 0);
    text-decoration: solid;
    cursor: pointer;
    transform: translateY(-10px);
}
.register-btn{
    background: violet;
    color: #000;
    padding: 8px 20px;
    border-radius: 20px;
    text-decoration: none;
    font-size: 14px;
}
.register-btn:hover{
    color:#0d090d;
    background-color: #ff5361;
    transform: scale(1.2);
    border-color: #070404;
}
.login-btn{
    background:violet;
    color: #000;
    padding: 8px 20px;
    border-radius: 20px;
    text-decoration: none;
    font-size: 14px;
}
.login-btn:hover {
    color:#0d090d;
    background-color: #ff5361;
    transform: scale(1.2);
    border-color: #070404;
      }
    
.container{
    padding: 0 7%;
}
.header h1{
    font-size: 4vw;
    font-weight: 500;
    color: #fff;
    text-align: center;
    padding-top: 22%;
}
.search-bar{
    background: #fff;
    width: 70%;
    margin: 30px auto;
    padding: 6px 10px 6px 30px;
    border-radius: 50px;
}
.search-bar:hover{
    cursor: pointer;
}
.search-bar form{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    
}
.search-bar form input{
    display: block;
    border: 0;
    outline: none;
    background: transparent;
    cursor: pointer;
}
.search-bar form button{
    background: #ff5361;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    border: 0;
    outline: none;
    cursor: pointer;
}
.search-bar form button img{
    width: 15px;
    margin-top: 7px;
}
.location-input{
    flex: 1;
}
.search-bar form label{
    font-weight: 600;
}
.sub-title{
    margin: 50px 0 20px;
    font-size: 2.2vw;
    font-weight: 500;
    color: #333;
}
.Exclusives{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px, 1fr));
    grid-gap: 30px;
}
.Exclusives div img{
    width: 100%;
    border-radius: 10px;
    display: inline-block;
    transition: transform 0.3s ease;
}

.Exclusives div img:hover{
    transform: translateY(-20px);
    cursor:pointer;

}
.Exclusives div{
    position: relative;
}
.Exclusives div span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    color: #fff;
}

.trending{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px, 1fr));
    grid-gap: 30px;
    margin-bottom: 30px;
}
.trending div img{
    width: 100%;
    border-radius: 10px;
}
.trending h3{
    color: #000;
    text-decoration: none;
    display: inline-block;
    transition: transform 0.3s ease;
    font-weight: 600;
    margin-top: 10px;
}
.trending h3:hover{
   color: red;
   text-decoration: underline;
   cursor: pointer;
   transform: translateY(-10px);
}

.stories{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px, 1fr));
    grid-gap: 30px;
    margin-bottom: 30px;
}
.stories div img{
    width: 100%;
    border-radius: 10px;
}
.stories div{
    position: relative;
    text-align: center;
}
.stories p{
    width:80% ;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    font-size: 26px;
}
.start-btn{
    text-decoration: none;
    background: #ff5361;
    color: #fff;
    width: 80%;
    max-width: 350px;
    display: block;
    text-align: center;
    margin: 60px auto;
    padding: 15px;
    border-radius: 30px;
    font-size: 20px;
} 
.footer{
   text-align: center;
}

.footer span{
    margin: 15px;
    padding: 25px;
    display: inline-flex;
    width: auto;
    font-size: 2.1rem;
    transition: 0.3s ease;
}

.footer span:hover{
    cursor: pointer;
    transform: translateY(-10px);
    color: rgba(197, 23, 241, 0.785);
}